.Home {
  width: 100%;
  height: 107vh;
  padding: 30px;
  background-color: #38cb481a;
  background-image: url("https://xtratheme.com/elementor/supermarket/wp-content/uploads/sites/106/2023/04/bg-hero.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.rightHome {
  width: 70%;
  height: 100%;
  z-index: 23;
}
.rightHome img {
  width: 95%;
  height: 85%;
  margin-top: 50px;
  z-index: 344;
}

.secondH1 {
  font-size: 85px;
  font-weight: 900;
}
.paragraph {
  font-size: 25px;
  font-weight: 900;
  color: #7a7a7a;
}
.buttonHome {
  padding: 0px 30px !important;
  background-color: #173850 !important;
  color: white !important;
  height: 80px !important;
  font-size: 22px !important;
  font-weight: 900 !important;
  transition: all 0.5s !important;
}
.buttonHome:hover {
  background-color: #01e281 !important;
  color: #173850 !important;
}
.buttonHome2 {
  padding: 0px 30px !important;
  background-color: white !important;
  color: #173850 !important;
  font-size: 24px !important;
  height: 80px !important;
  font-weight: 900 !important;
  transition: all 0.7s ease-in-out !important;
  border: 2px solid #173850 !important;
}
.buttonHome2:hover {
  background-color: #01e281 !important;
  border: none !important;
}
.bottomBar {
  background-color: white;
  border-radius: 100px;
  box-shadow: 2px 2px 35px 2px #c9c7c7;
  margin: auto;
  padding: 10px 50px;
  transform: translateY(-126px);
  flex-wrap: wrap;
}
.sectionThreeHome {
  display: flex;
  align-items: center;
  gap: 60px;
  width: 90%;
  margin: auto;
}
.BigCardTwo {
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: contain;
  min-width: 135% !important;
  max-height: 450px !important;
  text-align: left !important;
  padding-top: 50px !important;
  padding-left: 50px !important;
  padding-right: 50px !important;
  margin-top: 20px !important;
  transform: translate(-1268px, 438px) !important;
}
.viewProduct {
  font-size: 30px;
  font-weight: 900;
}
.CardsTypeProduct {
  display: flex;
  align-items: center;
  gap: 100px;
  flex-wrap: wrap;
  padding-left: 60px;
}
.forSectionHome {
  background-color: #01e281;
  background-image: url("https://xtratheme.com/elementor/supermarket/wp-content/uploads/sites/106/2023/05/box-bg.jpg");
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 100px;
  margin-left: 50px;
  border-radius: 50px;
  padding-left: 100px;
  height: 85vh !important;
  width: 95%;
}
.forSectionHome img {
  transform: translate(60px, 63px);
}
.titleSquare {
  font-size: 72px;
  color: white;
}
.descriptionSquare {
  font-size: 90px;
  color: white;
  font-weight: 900;
}
.textSquare {
  font-size: 30px;
  font-weight: 500;
  width: 87%;
  color: #e7e6e6;
}
.appleStore,
.googlePlay {
  width: 250px;
  height: 100px;
}
