.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  max-width: 100% !important;
}
.iconContact {
  font-size: 100px;
  background-color: #01e281;
  border-radius: 50%;
  padding: 20px;
  color: white;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
.containerInputs {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}
.iconContact:hover {
  color: #01e281;
  background-color: white;
}
.iconsContact {
  display: flex;
  align-items: center;
  gap: 250px;
  width: 100%;
  margin-left: 550px;
  flex-wrap: wrap;
}
.iconsContact h4 {
  font-size: 25px;
  font-weight: 400;
}
.bigBox {
  height: 149vh;
  background-color: #122d40;
  position: relative;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  align-items: left;
  padding: 50px;
  margin-top: 100px;
}
.inputs {
  display: flex;
  align-items: center;
  gap: 20px;
  width: 100%;
  margin-top: 100px;
}
.textArea {
  margin-top: 50px;
}
.btnSend {
  width: 250px;
  height: 10px;
  text-align: center;
  font-weight: 900 !important;
}
.labelcontact {
  display: flex;
  align-items: center;
  color: #7a7a7a;
  transform: translate(-153px, 20px);
  margin-bottom: 40px;
  font-size: 20px;
  font-weight: 900;
  width: 135px;
}
.bigBox2 {
  position: absolute;
  top: 97%;
  left: -100px;
  z-index: -876;
  transform: skew(-34deg, -5deg);
  opacity: 0.5;
  scale: 0.8;
  animation: b2 4s 2s ease-in-out infinite alternate;
}
@keyframes b2 {
  from {
    transform: skew(-34deg, -5deg);
  }
  to {
    transform: skew(34deg, 5deg);
  }
}
.bigBox:last-child {
  position: absolute;
  top: 97%;
  right: -100px;
  scale: 0.8;
  z-index: -876;
  transform: rotate(37deg);
  opacity: 0.5;
  animation: b3 4s 2s ease-in-out infinite alternate;
}
@keyframes b3 {
  from {
    transform: rotate(37deg);
    transform: skew(-34deg, -5deg);
  }
  to {
    transform: rotate(-37deg);
    transform: skew(34deg, 5deg);
  }
}
@media (min-width: 1200px) and (max-width: 1600px) {
  .iconsContact {
    display: flex;
    align-items: center;
    gap: 2px;
    width: 123%;
    flex-wrap: wrap;
  }
  .iconContact {
    font-size: 74px;
    background-color: #01e281;
    border-radius: 50%;
    padding: 20px;
    color: white;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
  }
  .inputs {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
    margin-top: 100px;
  }
  .input {
    border: none;
    border-radius: 30px;
    width: 343px !important;
    font-size: 25px;
    transform: translateX(-57px);
    height: 60px;
    padding-left: 30px;
  }
  .labelcontact {
    display: flex;
    align-items: center;
    color: #7a7a7a;
    transform: translate(-146px, 45px);
    margin-bottom: 40px;
    font-size: 20px;
    font-weight: 900;
    width: 135px;
  }
  .iconsContact h4 {
    font-size: 18px;
    font-weight: 400;
  }
  .containerInputs {
    width: 402px !important;
    display: flex;
    align-items: center;
    gap: 20px;
    margin: 0px 38px;
  }
}
@media (min-width: 992px) and (max-width: 1023px) {
  .contact {
    gap: 30px;
  }

  .iconContact {
    font-size: 80px;
    padding: 15px;
  }

  .iconsContact {
    gap: 150px;
    margin-left: 300px;
  }

  .bigBox {
    width: 80%;
    height: auto;
    padding: 30px;
    margin-top: 50px;
  }

  .inputs {
    margin-top: 50px;
  }

  .btnSend {
    width: 200px;
    height: 45px;
  }

  .bigBox2 {
    top: 90%;
    left: -80px;
    scale: 0.7;
    animation: b2 4s 2s ease-in-out infinite alternate;
  }

  .bigBox:last-child {
    top: 90%;
    right: -80px;
    scale: 0.7;
    animation: b3 4s 2s ease-in-out infinite alternate;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  .contact {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .iconContact {
    font-size: 60px;
    padding: 10px;
  }

  .iconsContact {
    gap: 50px;
    margin-left: 0;
  }

  .bigBox {
    width: 90%;
    height: auto;
    padding: 20px;
    margin-top: 30px;
  }

  .inputs {
    flex-direction: column;
    margin-top: 30px;
  }

  .btnSend {
    width: 150px;
    height: 40px;
  }

  .bigBox2 {
    top: 85%;
    left: -50px;
    scale: 0.6;
    animation: b2 4s 2s ease-in-out infinite alternate;
  }

  .bigBox:last-child {
    top: 85%;
    right: -50px;
    scale: 0.6;
    animation: b3 4s 2s ease-in-out infinite alternate;
  }
}

@media screen and (max-width: 767px) {
  .contact {
    gap: 10px;
  }

  .iconContact {
    font-size: 40px;
    padding: 5px;
  }
  .containerInputs {
    width: 292px !important;
    display: flex !important;
    align-items: center !important;
    gap: 20px;
    flex-wrap: wrap !important;
  }
  .iconsContact {
    flex-direction: column;
    align-items: center;
    gap: 20px;
    margin-left: 0;
  }

  .iconsContact h4 {
    font-size: 15px;
    color: #27638d;
  }

  .bigBox {
    width: 100%;
    height: auto;
    padding: 51px;
  }

  .inputs {
    flex-direction: column;
    margin-top: 20px;
  }

  .btnSend {
    width: 148px;
    height: 35px;
  }

  .bigBox2 {
    display: none;
  }

  .bigBox:last-child {
    display: none;
  }
}
