.ProductInfo{
    width: 100%;
    height: 200vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.LeftRightSections{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 180px;
    flex-wrap: wrap;
}
.containerImg{
    width: 100%;
    padding: 20px;
    height: 100%;
}
.BigText,
.infoForProduct{
    color: #676767;
    line-height: 1.75;
    font-size: 30px;
}
.containerImg img{
    width: 90%;
    height: 90%;
    box-shadow: 2px 2px 434px 43px #a7a7a7;
    border-radius: 40px;
}
.leftProduct{
    width: 40%;
    height: 100vh;
    padding-left: 20px;
}
.rightProduct{
    width: 50%;
    padding-left: 50px;
    height: 100vh;
    padding-top: 70px;
}
.Buttons{
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 50px;
    margin-top: 10px;
}
.biggButton{
    width: 250px;
    height: 70px;
    font-size: 25px;
    margin-right: 50px;
}
.shuffle,.Heart{
    width: 70px;
    height: 70px;
    background-color: #676767;
    color: white;
    font-size: 20px;
    border-radius: 50%;
    padding: 15px;
}
.SocialLinks{
    width: 70%;
    background-color: #dddddd;
    text-align: center;
    padding: 30px;
    padding-left: 50px;
    font-size: 30px;
    display: flex;
    gap: 30px;
    border-radius: 40px;
}
.iconS{
    transition: all 0.3s ease-in-out;
}
.iconS:hover{
    rotate: 360deg !important;
    scale: 1.5;
}
@media (min-width: 468px) and (max-width: 1568px){
    .ProductInfo {
        width: 100%;
        height: 337vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .leftProduct {
        width: 100%;
        height: 100vh;
        padding-left: 20px;
    }
    .rightProduct {
        width: 93%;
        padding-left: 50px;
        height: 100vh;
        padding-top: 70px;
    }
    .SocialLinks {
        width: 105%;
        background-color: #dddddd;
        text-align: center;
        padding: 31px;
        padding-left: 41px;
        margin-top: 245px;
        font-size: 52px;
        display: flex;
        gap: 30px;
        border-radius: 40px;
    }
}
