@media (max-width: 468px){
    .ProductInfo {
        width: 100%;
        height: 313vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .increaseAndDicreas{
        scale: 0.8;
    }
    .leftProduct {
        width: 100%;
        height: 100vh;
        padding-left: 20px;
    }
    .rightProduct {
        width: 93%;
        padding-left: 50px;
        height: 100vh;
        padding-top: 70px;
    }
    .infoForProduct {
        color: #676767;
        line-height: 1.75;
        font-size: 18px;
    }    
    .SocialLinks {
        display: none;
    }
    .rowInfo{
        width: 285px !important;
        display: flex !important;
        position: relative !important;
        align-items: center !important;
    }
}